<!--教室设置-->
<template>
  <div class="room-setting">
    <base-title title="教室设置">
      <template slot="filterTree">
        <!--<slelct-tree :currentSelect="currentSelect" :treeList="treeList"></slelct-tree>-->
        <slelct-Tree :rootId="rootId" @node-select="orgSelect" style="margin-left: 15px"></slelct-Tree>
      </template>
      <template slot="btn">
        <btn-list @btn-click="handleAdd" :model="{icon:'icon-add',name:'添加'}"></btn-list>
      </template>
    </base-title>

    <div class="content">
      <base-table
        :tableData="tableData"
        :tableColumn="tableColumn"
        @on-stop="handleStop"
        @on-edit="handleEdit">
      </base-table>
    </div>

    <div class="footer">
      <state-switch @switchL="handleSwicthState"></state-switch>
      <page :pageTotal="pageTotal" @handleSizeChange="SizeChange" @handleCurrentChange="CurrentChange"></page>
    </div>

 <!-- modal -->
    <div class="room-setting-modal">
      <el-form style="width:330px;margin:24px auto" ref="formInfo" :model="formInfo" label-width="80px" :rules="formRules">
        <el-form-item label="机构" prop="orgId">
          <org-Select :rootId="orgId"  :initText="orgSelectText" :refreshText="refreshText"   @node-select="handleOrgSelect"></org-Select>
         <!-- <el-select v-model="formInfo.title" style="width:100%">
            <el-option label="区域一"  value="shanghai"></el-option>
            <el-option label="区域e"  value="shanghai2"></el-option>
          </el-select>-->
        </el-form-item>
        <el-form-item label="教室名称" prop="name">
          <el-input v-model="formInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="教室类型" prop="area">
          <el-select v-model="formInfo.area" style="width:100%" @focus="setSelectMinWidth">
            <el-option  v-for="item in classroomType" :key="item.code" :label="item.text" :value="item.code" :style="{'min-width': selectMinWidth + 2 + 'px'}"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属建筑" prop="desc">
          <el-select v-model="formInfo.desc" style="width:100%" @focus="setSelectMinWidth">
            <el-option  v-for="item in build" :key="item.id" :label="item.name" :value="item.id" :style="{'min-width': selectMinWidth + 2 + 'px'}"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="显示顺序">
          <el-input v-model="formInfo.order"></el-input>
        </el-form-item>
      </el-form>
    </div>


  </div>
</template>
<script>
export default {
    components:{
        stateSwitch:() => import("@/components/state-switch"),
        baseTable:()=>import("@/components/table"),
        slelctTree: () => import("./../../../components/org-menu"),
        orgSelect: () => import("./../../../components/org-select"),
        baseTitle:()=>import("@/components/title"),
        btnList:()=>import("@/components/btn-list"),
        page:()=>import("@/components/paging")
    },
    data(){
        return {
            rootId:'0',
            pageIndex:1,
            pageSize:10,
            status:0,
            pageTotal:0,
            tableColumn:[
                {
                    prop:'orderIndex',
                    label:'显示顺序'
                },
                {
                    prop:'name',
                    label:"教室名称",
                },
                {
                    prop:'classRoomTypeIdText',
                    label:'教室类型'
                },
                {
                    prop:'orgName',
                    label:'机构'
                },
                {
                    prop:'buildingsName',
                    label:'所属建筑'
                },
                {
                    prop:'campusName',
                    label:'校区'
                },
                {
                    prop:'statusText',
                    label:'状态'
                }
            ],
            tableData:[],
            //add机构
            selectMinWidth:0,
            classroomType:[],
            build:[],
            orgId:'0',
            enable:0,
            orgSelectText:'',
            refreshText:false,
            /* dialog */
            formInfo:{
                orgId:'',
                title:'',
                name:'',
                area:'',
                desc:'',
                order:''
            },
            formRules:{
                orgId:[{required: true, message: '请选择学校名称', trigger: 'blur'}],
                name:[{required: true, message: '请输入教室名称', trigger: 'blur'}],
                area:[{required: true, message: '请选择教室类型', trigger: 'change'}],
                desc:[{required: true, message: '请选择所属建筑', trigger: 'change'}],

            }
        }
    },
    created() {
        this.settingData();
        this.dictionary();
    },
    methods:{
        setSelectMinWidth(val){
            let target = val.srcElement ? val.srcElement : val.target;
            this.selectMinWidth = target.clientWidth;
        },
        //头部树
        orgSelect(val){
            this.rootId = val.id;
            this.settingData();
        },
        //分页查询
        settingData(){
            let params = {
                pageIndex: this.pageIndex,
                pageSize: this.pageSize,
                status: this.status,
                orgId:this.rootId
            };
            this.$api.classroom(params).then(res => {
                if (res.success == true) {
                    //赋值分页总数
                    this.pageTotal = parseInt(res.totalDatas);
                    let array = [];
                    let list = res.data;
                    list.map((item,index)=>{
                        array.push(
                            Object.assign({
                                index:index+1
                            },item,{indexNum:'str'})
                        )
                    });
                    //赋值表格数据
                    this.tableData = array;
                } else {
                    this.$myLayer.errorLayer('失败')
                }
            })
        },
        //分页
        SizeChange(val) {
            this.pageSize = val;
            this.settingData();
        },
        CurrentChange(val) {
            this.pageIndex = val;
            this.settingData();
        },
        handleSwicthState(val){
            this.status = val;
            this.settingData();
        },

        //添加机构
        handleOrgSelect(val,callback){
            this.$refs['formInfo'].resetFields();
            this.formInfo.order = '';
            this.orgId = val.id;
            this.formInfo.orgId = val.id;
            this.areaSelect();
            callback(true);
        },
        //字典
        dictionary(){
            let sex = {"type": ['classroom_type']};
            this.$api.dictSelect(sex).then(res => {
                if(res.success == true){
                    let  i = 0;
                    for (i = 0; i < res.data.length; i++) {
                        if(res.data[i].type == 'classroom_type'){
                            this.classroomType = res.data[i].data;
                        }
                    }
                }
            });
        },
        //建筑
        areaSelect(){
            let sexT = {
                orgId:this.orgId
            };
            this.$api.areaSelect(sexT).then(res => {
                if(res.success == true){
                    this.build = res.data;
                }
            });
        },
        /* 添加 */
        handleAdd(){
            let _this = this;
            _this.formInfo.order = '';
            _this.orgId = '';
            _this.orgSelectText ='';
            _this.formInfo.orgId = '';
            _this.refreshText = !_this.refreshText;
            _this.$refs['formInfo'].resetFields();
            this.$myLayer.formLayer("新建",$('.room-setting-modal'),['422px'],function () {
                _this.$refs["formInfo"].validate((valid) => {
                    if (valid) {
                        let params = {
                            name: _this.formInfo.name,
                            classRoomTypeId:_this.formInfo.area,
                            orderIndex:_this.formInfo.order,
                            orgId:_this.orgId,
                            buildingId:_this.formInfo.desc
                        };
                        _this.$api.classroomAdd(params).then(res => {
                            if (res.success == true) {
                                _this.settingData();
                                _this.$myLayer.successLayer(res.msg)
                            } else {
                                _this.$myLayer.errorLayer(res.msg)
                            }
                        })
                    } else {
                        return false;
                    }
                })
            })
        },
        //编辑
        handleEdit(row){
            let _this = this;
            _this.$refs['formInfo'].resetFields();
            _this.formInfo.order = '';
            //机构
            _this.orgId = row.row.orgId;
            _this.orgSelectText =row.row.orgName;
            this.formInfo.orgId = row.row.orgId;
            _this.refreshText = !_this.refreshText;
            _this.areaSelect();
            if(row.row.classRoomTypeIdText != ''){
                _this.formInfo.area = row.row.classRoomTypeId;
            }
            if(row.row.buildingsName != ''){
                _this.formInfo.desc = row.row.buildingId;
            }
            _this.formInfo.name = row.row.name;
            _this.formInfo.order = row.row.orderIndex;
            this.$myLayer.formLayer("编辑",$('.room-setting-modal'),['422px'],function () {
                _this.$refs["formInfo"].validate((valid) => {
                    if (valid) {
                        let ediData = {
                            name: _this.formInfo.name,
                            classRoomTypeId:_this.formInfo.area,
                            orderIndex:_this.formInfo.order,
                            orgId:_this.orgId,
                            buildingId:_this.formInfo.desc,
                            id:row.row.id
                        };
                        _this.$api.classroomEdit(ediData).then(res => {
                            if (res.success == true) {
                                _this.settingData();
                                _this.$myLayer.successLayer(res.msg)
                            } else {
                                _this.$myLayer.errorLayer(res.msg)
                            }
                        })
                    } else {
                        return false;
                    }
                })
            })
        },
        //停用禁用
        handleStop(row){
            let _this = this;
            if(row.status != 0){
                _this.enable = 0;
            }else {
                _this.enable = 1;
            }
            let params = {
                id:row.id,
                status:_this.enable
            };
            this.$api.classroomDis(params).then(res => {
                if (res.success == true) {
                    _this.settingData();
                    this.$myLayer.successLayer(res.msg)
                } else {
                    this.$myLayer.errorLayer(res.msg)
                }
            })
        }
    }
}
</script>
<style>
  .el-select-dropdown,.el-picker-panel{
    z-index: 999999999!important;
    width: auto;
  }
</style>
<style lang="scss" scoped>
.room-setting {
 padding: 18px 24px;
 .content {
   margin-top: 24px;
 }
  .footer {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
  }

  .room-setting-modal {
    display: none;
  }
}
</style>
